html {
  font-size: 62.5%;
  color: rgb(26, 26, 26);
}

body {
  font-family: proxima-soft, sans-serif;
  font-size: 16px;
  font-style: normal;
  line-height: 1.375em;
  margin: 0;
}
h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  font-family: proxima-soft, sans-serif;
  font-weight: 500;
}

#app {
  display: flex;
}

